/* General Set-up */

:root{
    --clr-pink: #FF64AE;
    --clr-blue: rgb(9, 17, 86);
    --clr-gray: #8B8B8B;
    --clr-bg: #FFFFFF;
    ;
    
    --ff-all: 'Poppins', sans-serif;
    
}

* {
    margin: 0;
    box-sizing: border-box;
    outline: none;
}

html {
    scroll-behavior: smooth;
}

body {
    background-color: var(--clr-bg);  
    font-family: var(--ff-all); 
}
h1,h2,h3,h4,h5,h6, p{
    font-family: var(--ff-all);
}



/* Styles */

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 170px;
    width: 100%;
}

.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 80%;
}

.content {
    display: flex;
    flex-direction: row;
    width: 100%;
}

.content .left{
    width: 60%;
}

.content .right {
    margin-left: 200px;
    width: 40%;
}

.content h2{
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 125%;
    color: var(--clr-pink);
}

.content h3 {
    font-style: normal;
    font-weight: 600;
    font-size: 36px;
    line-height: 125%;
    color: var(--clr-blue);
    margin: 12px 0;
}

.content p {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.1em;
    color: var(--clr-gray);
    margin-bottom: 44px;
}

.form {
    width: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    margin-top: 30px;
}

.formLeft{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50%;
    margin-right: 100px;
}

.formLeft img{
    width: 100%;
}

/* Form input */
.formRight {
    width: 50%;
}

.formInput {
    display: flex;
    flex-direction: column;
}

.inputName input {
    width: 45%;
    height: 61.58px;
}

.inputName >:nth-child(1) {
    margin-right: 10%;
}

.formInput input, .formInput textarea{
    margin-bottom: 38.32px;
    font-family: var(--ff-all);
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.1em;
    background: #FFFFFF;
    color: var(--clr-blue);
    border: 1px solid #D9DDFE;
    border-radius: 15px;
    height: 61.58px;
    padding-left: 24px;
    transition: all ease .5s;
}

.formInput input:focus, .formInput textarea:focus{
    border: 1px solid var(--clr-pink);
}

.formInput textarea::placeholder {
    padding-top: 10px;
    color: var(--clr-gray);
}

.formRight button{
    width: 248px;
    height: 58px;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    font-family: var(--ff-all);
    text-align: center;
    letter-spacing: 0.1em;
    color: white;
    border: none;
    background: var(--clr-pink);
    box-shadow: 0px 17px 22px #FFEDF6;
    border-radius: 50px;
    transition: all ease .4s;
    cursor: pointer;
}

.formRight button:hover {
    color: #ffe0e0;
    box-shadow: #f36eae 0px 20px 30px -10px;
}

.background {
    position: absolute;
    z-index: -10;
    width: 100%;
    top: 300px;
    right: 0;
}
.map {
    margin-top: 100px;
    width: 100%;
}
/* Responsive Contact-us */

@media (max-width: 1200px) {
    .form {
        margin-top: 30px;
    }
    
    .formLeft img{
        margin-right: 30px;
    }
    
    .content h2{
        font-size: 16px;
    }
    
    .content h3 {
        font-size: 30px;
        margin: 12px 0;
    }
    
    .content p {
        font-size: 14px;
        margin-bottom: 34px;
    }
    
    .inputName input {
        width: 100%;
        height: 61.58px;
    }
    
    .formInput {
        display: flex;
        flex-direction: column;
    }
    
    .formInput input, .formInput textarea{
        margin-bottom: 30px;
        font-size: 14px;
        border-radius: 15px;
        height: 61.58px;
        padding-left: 24px;
    }
    
    .formInput textarea::placeholder {
        padding-top: 10px;
    }
    
    .formRight button{
        width: 220px;
        height: 50px;
        font-size: 14px;
        border-radius: 40px;
    }
    
    .formRight button:hover {
        color: #ffe0e0;
        box-shadow: #f36eae 0px 15px 25px -5px;
    }
}


