        border-radius: 40px;
    }
    
    .formRight button:hover {
        color: #ffe0e0;
        box-shadow: #f36eae 0px 15px 25px -5px;
    }
}

@media (max-width: 850px) {
    .content {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
        text-align: center;
    }
    
    .content .left{
        width: 100%;
    }
    
    .content .right {
        margin-left: 0px;
        width: 70%;
    }
    .form {
        margin-top: 20px;
    }
    
    .form h2{
        font-size: 16px;
    }
    
    .form h3 {
        font-size: 22px;
        margin: 12px 0;
    }
}

